* {
  box-sizing: border-box;
}

h2,
p {
  font-weight: 500;
}

h3 {
  font-weight: 700;
  font-size: 24px;
}

a {
  text-decoration: none;
  color: inherit;
}

.scroll-off {
  overflow-y: hidden;
}

ul {
  list-style: none;
  padding: 0;
}

h1,
h2,
h3,
ul,
p {
  margin: 0;
}
