.about-us {
  padding: 60px 0;

  &__title {
    text-align: start;
    width: max-content;
    margin-right: 24px;

    @include onTablet {
      margin-top: -5px;
      grid-column: 1 / 4;
    }

    @include onDesktop {
      margin-top: -10px;
      grid-column: 1 / 6;
    }
  }

  &__text {
    font-weight: 500;
    font-size: 23px;
    line-height: 140%;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      width: 500px;
      grid-column: 6 / 12;
    }
  }
}
